.image-editor__container {
    @include wrapper(); }

.image-editor__wrapper {
    background-color: #f2f2f2;
    @include row-flex();
    padding-top: 55px;
    padding-bottom: 57px;
    justify-content: space-around;
    @include sm-block() {
        justify-content: center; } }

.image-editor__picture, .image-editor__block-tools {
    @include col();
    @include size-md(6);
    @include size-sm(12); }

.image-editor__picture {
    margin-right: 75px;
    @include md-block() {
        margin-right: 15; }
    @include sm-block() {
        margin-bottom: 42px; }
    @include xs-block();
    margin-right: 15px; }


.image-editor__download-image {
    @include sm-block() {
        display: block;
        margin: 0 auto; } }

.image-editor__tools {
    @include xs-block() {
        display: flex;
        position: relative;
        justify-content: space-around;
        margin-bottom: 38px;
        padding-bottom: 75px;
        border-bottom: 2px solid #dedfe1; } }

.image-editor__crop, .image-editor__fill, .image-editor__contrast {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 15px;
    padding-bottom: 13px;
    border-bottom: 1px solid #dedfe1;
    @include md-block() {
        margin-bottom: 27px;
        padding-bottom: 24px; }
    @include xs-block() {
        border-bottom: none;
        margin-bottom: 0; } }

.image-editor__crop-image {
    @include md-block() {
        width: 22px;
        height: 22px; }
    @include sm-block() {
        width: 44px;
        height: 44px; } }

.image-editor__fill-image {
    @include md-block() {
        width: 24px;
        height: 22px; }
    @include sm-block() {
        width: 48px;
        height: 43px;
        opacity: 0.2; } }

.image-editor__contrast-image {
    @include md-block() {
        width: 16px;
        height: 16px; }
    @include sm-block() {
        width: 32px;
        height: 32px;
        opacity: 0.2; } }

.image-editor__contrast {
    margin-bottom: 163px;
    @include md-block() {
        margin-bottom: 79px; }
    @include xs-block() {
        margin-bottom: 0; } }

.image-editor__crop img, .image-editor__fill img, .image-editor__contrast img {
    margin-right: 48px;
    @include xs-block() {
        margin-right: 0; } }

.range {
    width: 288px;
    height: 2px;
    appearance: none;
    background-color: #283645;
    @include xs-block() {
        width: 85%; } }

input[type=range]::-moz-range-thumb {
    appearance: none;
    width: 14px;
    height: 14px;
    background-color: #f2f2f2;
    border: 2px solid #d22856;
    border-radius: 50%; }

.range-crop {
    @include xs-block() {
        position: absolute;
        bottom: 25%;
        left: 50%;
        transform: translateX(-50%); } }

.range-fill, .range-contrast {
    @include xs-block() {
        display: none; } }

.image-editor__button {
    width: 183px;
    box-sizing: border-box;
    border: 3px solid #d22856;
    border-radius: 20px;
    padding: 15px 0;
    font-size: 18px;
    font-weight: 700;
    line-height: 18px;
    color: #d22856;
    @include md-block() {
        display: block;
        margin: 0 auto;
        width: 85%;
        border-radius: 29px; } }

.image-editor__button--post {
    margin-right: 40px;
    @include md-block() {
        margin-bottom: 24px; } }

.image-editor__button--cancel {
    opacity: 0.2; }


