.page--form .main-footer {
    @include sm-block() {
        display: block;
        padding-bottom: 40px; } }

.page-footer__logo, .footer-icon__wrapper, .footer-developed__wrapper {
    @include size-sm(12); }

.page-footer__logo-img {
    @include sm-block() {
        margin: 0 auto;
        display: block;
        margin-bottom: 45px; } }

.footer-icon__wrapper {
    @include sm-block() {
        justify-content: center;
        margin-bottom: 42px; } }

.footer-icon__wrapper a:nth-child(1) {
    @include sm-block() {
        margin-right: 18px; } }

.footer-icon__wrapper a:nth-child(2) {
    @include sm-block() {
        margin-right: 18px; } }

.footer-developed__wrapper {
    @include sm-block() {
        justify-content: center; } }

